    </transition>

    <jw-pagination :items="filteredClients" @changePage="onChangePage" 
                    :initialPage="initialPage" :pageSize="pageSize" 
                    :labels="customLabels" :styles="customStyles" class="pagine">
    </jw-pagination>

  </div>
</template>

<script>
  import { mapGetters, mapActions } from 'vuex';
  import Loading from '@/components/utils/Loading.vue';
  import { customLabels, customStyles } from '@/components/utils/pageNav.js';
  import navigation from '../mixins/navigation';
  import navigationSearch from '../mixins/navigationSearch';
  import SearchBar from '@/components/utils/SearchBar.vue';

  export default {
    name: 'Clients',

    components: {
      Loading,
      SearchBar
    },

    data() {
      return {
        customLabels,
        customStyles,
      }
    },

    mixins: [
      navigation,
      navigationSearch
    ],

    computed: {
      ...mapGetters([ 'getAllClients', 
                      'getAllSchedules',
                      'getClientsPage',
                      'getClientsPageSize',
                      'loadingState' ]),
      activeClients: function () {
        return this.getAllClients.filter(a => a.active === true)
      }
    },

    watch: {
      pageSize() { this.searchClients() }
    },

    methods: {
      ...mapActions([ 'fetchClients', 
                      'fetchClient',
                      'fetchClientsPage',
                      'fetchClientsPageSize',
                      'fetchFromForm',
                      'clientClear',
                      'fetchSchedules',
                      'formTypeChange',
                      'setLoadingState' ]),

      setPageSize(val) {
        this.pageSize = val;
        this.fetchClientsPageSize(val);
      },
      
      async searchClients(val = '') {
        await this.initClients();
        let mu = this.getAllClients.filter(post => {
          return post.name.toLowerCase().includes(val.toLowerCase()) || 
                  post.email.toLowerCase().includes(val.toLowerCase()) || 
                  post.mobile.includes(val)
        });
        this.filteredClients = mu;
      },

      async newClient() {
        this.setLoadingState(true);
        this.formTypeChange('client');
        await this.clientClear();
      },

      setPageNr() {
        const element = document.querySelector('ul.pagination > li.active');
        if (element) this.fetchClientsPage(Number(element.innerText));
      },

      async selectClient(client) {
        this.setLoadingState(true);
        await this.fetchClient(client);
        this.setPageNr();
        this.formTypeChange('client');
      },

      async clientActivities(client) {
        this.setLoadingState(true);
        await this.fetchClient(client);
        this.fetchFromForm('clients');
        this.setPageNr();
        this.formTypeChange('clientactivity');
      },

      mapSchedules() {
        let obj, arr = []
        for (let i=0; i<this.getAllSchedules.length; i++) {
          for(let j =0; j<this.getAllSchedules[i].members.length; j++) {
            obj = {
              "title": this.getAllSchedules[i].title,
              "startTime": this.getAllSchedules[i].startTime,
              "client": this.getAllSchedules[i].members[j].client
            }
            arr.push(obj)
          }
        }
        return arr
      },

      setClientSchedule(id) {
        let sche = '';
        sche = this.mapSchedules().filter(post => {
          return post.client._id == id
        });
        return sche[0] ? sche[0].title + '/' + sche[0].startTime : '';
      },

      async initClients() {
        await this.fetchClients();
        this.filteredClients = this.getAllClients.sort((a, b) => 
                                (a.last_name.toLowerCase() > b.last_name.toLowerCase() ? 1 : -1));
        if (this.getClientsPage !== 1) this.initialPage = this.getClientsPage;
        if (this.getClientsPageSize !== 10) this.pageSize = this.getClientsPageSize;
        await this.fetchSchedules();
      }
    },

    async mounted() {
      await this.initClients();
      this.setLoadingState(false);
    }
  }
</script>

